// ======================================
// MIXINS
// ======================================

// Button mixin
@mixin button(
  $bg-color: $bg-color-footer-dark,
  $text-color: white,
  $padding: 0.75rem 1.5rem
) {
  display: inline-block;
  background-color: $bg-color;
  color: $text-color;
  padding: $padding;
  border: none;
  border-radius: 4px;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.3s ease;

  &:hover {
    background-color: darken($bg-color, 10%);
  }
}

// Responsive two-column grid mixin
@mixin two-column-grid(
  $gap-mobile: 10px,
  $gap-tablet: 20px,
  $gap-desktop: 30px,
  $gap-large: 40px,
  $breakpoint: 990px // updated breakpoint
) {
  display: grid;
  grid-template-columns: 1fr; // default single column
  gap: $gap-mobile;

  @media (min-width: $breakpoint) {
    grid-template-columns: repeat(2, 1fr); // two columns on tablet+
    gap: $gap-tablet;
  }

  @media (min-width: 1260px) {
    gap: $gap-large;
  }
}

// Full width section mixin
@mixin full-width($bg-color: transparent) {
  width: 100%;
  background-color: $bg-color;
  margin: 0 auto;
}

// Footer button mixin
@mixin square-btn-footer($size) {
  width: $size;
  height: $size;
  display: flex;
  align-items: center;
  justify-content: center;
}

// ======================================
// Nav-Bar Top

// ✅ Responsive logo heights at breakpoints
@mixin responsive-logo($desktop, $tablet, $mobile, $xsmall) {
  height: $desktop;

  @media (max-width: 992px) {
    height: $tablet;
  }
  @media (max-width: 767px) {
    height: $mobile;
  }
  @media (max-width: 480px) {
    height: $xsmall;
  }
}

// ✅ Creates a square button (used for icons)
@mixin square-btn-nav($size) {
  width: $size;
  height: $size;
}

// ✅ Creates a rectangle button (e.g., hamburger)
@mixin rectangle-btn($w, $h) {
  width: $w;
  height: $h;
}

// ✅ Sets button width + height + line-height
@mixin button-dimensions($w, $h) {
  width: $w;
  height: $h;
  line-height: $h;
}
